<template><section><html><head></head><body><h1>Primitive</h1>
<p><code v-pre="">vc-primitive</code> 用于加载普通图元，再通过它加载几何图形。</p>
<h2>示例</h2>
<h3>加载普通图元</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive @click="clicked" :appearance="appearance" :geometry-instances="geometryInstances"></vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
