        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span>

        <span class="hljs-variable language_">this</span>.<span class="hljs-property">geometryInstances</span> = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">GeometryInstance</span>({
          <span class="hljs-attr">geometry</span>: <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">RectangleGeometry</span>({
            <span class="hljs-attr">rectangle</span>: <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Rectangle</span>.<span class="hljs-title function_">fromDegrees</span>(
              <span class="hljs-variable language_">this</span>.<span class="hljs-property">rectangle</span>.<span class="hljs-property">west</span>,
              <span class="hljs-variable language_">this</span>.<span class="hljs-property">rectangle</span>.<span class="hljs-property">south</span>,
              <span class="hljs-variable language_">this</span>.<span class="hljs-property">rectangle</span>.<span class="hljs-property">east</span>,
              <span class="hljs-variable language_">this</span>.<span class="hljs-property">rectangle</span>.<span class="hljs-property">north</span>
            )
