        })
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">appearance</span> = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">MaterialAppearance</span>({
